.Skills {
  justify-content: space-evenly;
  position: absolute;
  bottom: 70px;
  width: 80%;
}

.Skills .list {
  padding: 0px;
  display: grid;
  grid-gap: 8px;
}

.Skills .list .list__elem {
  display: flex;
  padding: 3px;
  margin: 2px;
}

.SkillLevel {
  display: flex;
  align-items: center;
}

.skill-level-elem {
  margin: 2px;
}

@media (min-width: 1200px) {
  .Skills {
    border-top: none;
  }

  .SkillLevel {
    display: flex;
    align-items: center;
  }

  .Skills .list-header__container2 {
    border-left: none;
  }

  .Skills .list {
    grid-template-columns: repeat(8, 1fr);
    grid-gap: 0px;
  }
}
